const months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
];

function formatDate(sourceDate) {
    let formattedDate = new Date(sourceDate);
    return `${
        months[formattedDate.getUTCMonth()]
    } ${formattedDate.getUTCFullYear()}`;
}

